<template>
  <div>
    <v-slide-group
      class="pa-4"
      show-arrows
    >
      <v-slide-item
        v-for="(list, key) in details"
        :key="key"
      >
        <v-card
          class="ma-1"
          width="200"
        >
          <v-img
            :src="image(list.person.image)" 
            class="align-end"
            lazy-src="@/assets/image-not-found.jpeg"
            height="250"
          >
            <v-card-title v-if="!list.person.image">
              <span class="grey darken-0">Image not found</span>
            </v-card-title>
            <v-card-title class="title text-center">
              <span class="grey darken-0">{{ list.person.name }}</span>
            </v-card-title>
            <v-card-subtitle
              v-if="list.type"
              class="text-center"
            >
              <span class="grey darken-0">{{ list.type }}</span>
            </v-card-subtitle>
          </v-img>
        </v-card>
      </v-slide-item>
    </v-slide-group>
  </div>
</template>

<script>
export default {
  name: "Team",
  props: {
    details: {
      type: Array,
      required:true
    },
  },
  methods: {
    image(value) {
      if (value ?? false) {
        return value.medium;
      }
    },
  },
};
</script>