<template>
  <div id="header">
    <v-toolbar short>
      <v-toolbar-title>
        <router-link
          to="/"
          :class="{'light':!$vuetify.theme.dark,'dark':$vuetify.theme.dark}"
        >
          TV Shows
        </router-link>
      </v-toolbar-title>
      <v-spacer />
      <div class="padding">
        <v-text-field
          v-if="showSearchBar"
          v-model="searchQuery"
          rounded
          outlined
          dense
          label="Search Shows"
          clearable
          @input="searchQueryChanged"
        />
      </div>
      <v-btn icon>
        <v-icon @click="showSearchBar = !showSearchBar">
          mdi-magnify
        </v-icon>
      </v-btn>
      <v-switch
        v-model="dark"
        class="mt-4"
        label="Dark"
        color="white"
      />
    </v-toolbar>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      showSearchBar: false,
      searchQuery: "",
      dark: false,
    };
  },
  watch: {
    dark() {
      this.$vuetify.theme.dark = this.dark;
    },
  },
  methods: {
    searchQueryChanged() {
      if (this.searchQuery === null) {
        this.searchQuery = "";
      }
      if (this.searchQuery.length) {
        this.$router.push({ name: "search", params: { id: this.searchQuery } });
      } else if (!this.searchQuery.length && this.$route.name !== "home") {
        this.$router.push("/");
      }
    },
  },
};
</script>

<style scoped>
.padding {
  margin-top: 30px;
  margin-right: 20px;
  size: 50px;
}
.dark {
  text-decoration: none;
  color: white;
}
.light {
  text-decoration: none;
  color: black;
}
</style>
